import "./Login.css";
import { useState } from "react";
import PersonIcon from "@mui/icons-material/Person";
import { Link } from "react-router-dom";
import VisibilityIcon from "@mui/icons-material/Visibility";
import VisibilityOffIcon from "@mui/icons-material/VisibilityOff";
import Nav from "../navbar/Nav";

const Login = () => {
  const [typelgn, setTypelgn] = useState("password");
  const [iconlgn, setIconlgn] = useState(<VisibilityOffIcon />);

  const handleTogglelgn = () => {
    if (typelgn === "password") {
      setIconlgn(<VisibilityIcon />);
      setTypelgn("text");
    } else {
      setIconlgn(<VisibilityOffIcon />);
      setTypelgn("password");
    }
  };

  return (
    <div className="bodylgn1">
      <Nav />
      <div className="login">
        <form className="login-form">
          <p className="login-font">Login</p>
          <div className="name input-fieldlgn">
            <span>
              <label htmlFor="name" className="font">
                Username
              </label>
            </span>
            <span className="icon">
              <PersonIcon />
            </span>
            <br></br>
            <input type="text" id="name" required></input>
            <div className="screen1"></div>
          </div>
          <div className="password input-fieldlgn">
            <span>
              <label htmlFor="pass" className="font">
                Password
              </label>
            </span>
            <span className="icon eyeicon" onClick={handleTogglelgn}>
              {iconlgn}
            </span>
            <br></br>
            <input type={typelgn} id="pass" required></input>
            <div className="screen2"></div>
            <div className="forgot-pwd">
              <Link to="/forgotpassword" className="font1">
                Forgot Password?
              </Link>
            </div>
          </div>
          <div className="btnlgn">
            <Link to="/">
              <button type="submit">login</button>
            </Link>
          </div>
          <div className="sign-uplgn">
            <span>Don't have an account? </span>
            <span>
              <Link to="/signup" className="font1">
                Sign Up
              </Link>
            </span>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Login;
