import "./Resetpwd.css";
import { useState } from "react";
import { Link } from "react-router-dom";
import VisibilityIcon from "@mui/icons-material/Visibility";
import VisibilityOffIcon from "@mui/icons-material/VisibilityOff";
import Nav from "../navbar/Nav";

const Resetpwd = () => {
  const [typerpwd1, setTyperpwd1] = useState("password");
  const [iconrpwd1, setIconrpwd1] = useState(<VisibilityOffIcon />);

  const handleTogglerpwd1 = () => {
    if (typerpwd1 === "password") {
      setIconrpwd1(<VisibilityIcon />);
      setTyperpwd1("text");
    } else {
      setIconrpwd1(<VisibilityOffIcon />);
      setTyperpwd1("password");
    }
  };

  const [typerpwd2, setTyperpwd2] = useState("password");
  const [iconrpwd2, setIconrpwd2] = useState(<VisibilityOffIcon />);

  const handleTogglerpwd2 = () => {
    if (typerpwd2 === "password") {
      setIconrpwd2(<VisibilityIcon />);
      setTyperpwd2("text");
    } else {
      setIconrpwd2(<VisibilityOffIcon />);
      setTyperpwd2("password");
    }
  };

  return (
    <div className="bodyrpwd1">
      <Nav />
      <div className="resetpwd">
        <form className="resetpwd-form">
          <p className="resetpwd-font">Reset Password</p>
          <div className="newpassword input-fieldrpwd">
            <span>
              <label htmlFor="newpass" className="font">
                New Password
              </label>
            </span>
            <span className="icon eyeicon" onClick={handleTogglerpwd1}>
              {iconrpwd1}
            </span>
            <br></br>
            <input type={typerpwd1} id="newpass" required></input>
            <div className="screen1"></div>
          </div>
          <div className="con-password input-fieldrpwd">
            <span>
              <label htmlFor="con-pass" className="font">
                Confirm Password
              </label>
            </span>
            <span className="icon eyeicon" onClick={handleTogglerpwd2}>
              {iconrpwd2}
            </span>
            <br></br>
            <input type={typerpwd2} id="con-pass" required></input>
            <div className="screen2"></div>
          </div>
          <div className="btnrpwd">
            <Link to="/login">
              <button type="submit">Confirm</button>
            </Link>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Resetpwd;
