import "./Nav.css";
import { Link } from "react-router-dom";
import Logo from "../assets/foodiKo.png";
import HomeIcon from "@mui/icons-material/Home";

const Nav = () => {
  return (
    <div className="n-nav">
      <div>
        <img className="n-foodiko-logo" src={Logo} alt="loading"></img>
      </div>
      <div className="n-home">
        <Link className="n-link" to="/">
          <button>
            <span>
              <HomeIcon />
            </span>
            <spn>Home</spn>
          </button>
        </Link>
      </div>
    </div>
  );
};

export default Nav;
