import "./Signup.css";
import Nav from "../navbar/Nav";
import { useState } from "react";
import PersonIcon from "@mui/icons-material/Person";
import EmailIcon from "@mui/icons-material/Email";
import VisibilityIcon from "@mui/icons-material/Visibility";
import VisibilityOffIcon from "@mui/icons-material/VisibilityOff";
import { Link } from "react-router-dom";

const Signup = () => {
  const [typesgn1, setTypesgn1] = useState("password");
  const [iconsgn1, setIconsgn1] = useState(<VisibilityOffIcon />);

  const handleTogglesgn1 = () => {
    if (typesgn1 === "password") {
      setIconsgn1(<VisibilityIcon />);
      setTypesgn1("text");
    } else {
      setIconsgn1(<VisibilityOffIcon />);
      setTypesgn1("password");
    }
  };

  const [typesgn2, setTypesgn2] = useState("password");
  const [iconsgn2, setIconsgn2] = useState(<VisibilityOffIcon />);

  const handleTogglesgn2 = () => {
    if (typesgn2 === "password") {
      setIconsgn2(<VisibilityIcon />);
      setTypesgn2("text");
    } else {
      setIconsgn2(<VisibilityOffIcon />);
      setTypesgn2("password");
    }
  };

  return (
    <div className="bodysgn1">
      <Nav />
      <div className="signup">
        <form className="signup-form">
          <p className="signup-font">Create Account</p>
          <div className="name input-fieldsgn">
            <span>
              <label htmlFor="name" className="font">
                Username
              </label>
            </span>
            <span className="icon">
              <PersonIcon />
            </span>
            <br></br>
            <input type="text" id="name" required></input>
            <div className="screen1"></div>
          </div>
          <div className="email input-fieldsgn">
            <span>
              <label htmlFor="email" className="font">
                Email
              </label>
            </span>
            <span className="icon">
              <EmailIcon />
            </span>
            <br></br>
            <input type="email" id="email" required></input>
            <div className="screen2"></div>
          </div>
          <div className="password input-fieldsgn">
            <span>
              <label htmlFor="pass" className="font">
                Password
              </label>
            </span>
            <span className="icon eyeicon" onClick={handleTogglesgn1}>
              {iconsgn1}
            </span>
            <br></br>
            <input type={typesgn1} id="pass" required></input>
            <div className="screen3"></div>
          </div>
          <div className="con-password input-fieldsgn">
            <span>
              <label htmlFor="con-pass" className="font">
                Confirm Password
              </label>
            </span>
            <span className="icon eyeicon" onClick={handleTogglesgn2}>
              {iconsgn2}
            </span>
            <br></br>
            <input type={typesgn2} id="con-pass" required></input>
            <div className="screen4"></div>
          </div>
          <div className="btnsgn">
            <Link to="/">
              <button type="submit">Sign Up</button>
            </Link>
          </div>
          <div className="loginsgn">
            <span>Already have an account? </span>
            <span>
              <Link to="/login" className="font1">
                login
              </Link>
            </span>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Signup;
