import { useContext, useEffect, useState } from "react";
import { Cartpage } from "../Context";
import Cartlist from "./Cartlist";
import "./css/Cart.css";
import Navbar from "./Navbar";
import HomeIcon from "@mui/icons-material/Home";
import { Link } from "react-router-dom";

const Cart = () => {
  const { cart1 } = useContext(Cartpage);
  const [total, setTotal] = useState();
  useEffect(() => {
    setTotal(cart1.reduce((acc, curr) => acc + Number(curr.price), 0));
  }, [cart1]);

  return (
    <>
      <div className="cart-parent">
        <Navbar />
        <div className="cart-cont1">
          <br></br>
          <p>Cart</p>
          <br></br>
          <div className="cart-cont">
            {cart1.map((items) => (
              <Cartlist items={items} key={items.id} />
            ))}
          </div>
        </div>
        <div className="cart-foot">
          <div className="cart-foot1">
            <div className="cart-tot">
              Total <span>₹</span><span>{total}</span>
            </div>
            <button className="porder" onClick={()=>alert("Order has been placed")}>Place Order</button>
          </div>
        </div>
      </div>
      <div className="c-home-icon1">
        <div className="c-home-icon">
          <Link to="/">
            <HomeIcon className="c-home" />
          </Link>
        </div>
      </div>
    </>
  );
};

export default Cart;
