import "./Forgotpwd.css";
import EmailIcon from "@mui/icons-material/Email";
import DialpadIcon from "@mui/icons-material/Dialpad";
import { Link } from "react-router-dom";
import Nav from "../navbar/Nav";

const Forgotpwd = () => {
  return (
    <div className="bodyfpwd1">
      <Nav />
      <div className="forgotpwd">
        <form className="forgotpwd-form">
          <p className="forgotpwd-font">Forgot Password</p>
          <div className="email input-fieldfpwd">
            <span>
              <label htmlFor="email" className="font">
                Email
              </label>
            </span>
            <span className="icon">
              <EmailIcon />
            </span>
            <br></br>
            <input type="email" id="email" required></input>
            <div className="screen1"></div>
          </div>
          <div className="otp input-fieldfpwd">
            <span>
              <label htmlFor="OTP" className="font">
                OTP
              </label>
            </span>
            <span className="icon">
              <DialpadIcon />
            </span>
            <br></br>
            <input type="text" id="OTP" required></input>
            <div className="screen2"></div>
          </div>
          <div className="btnfpwd">
            <Link to="/resetpassword">
              <button type="submit">Reset</button>
            </Link>
          </div>
          <div className="otp-reqfpwd">
            <span>Don't receive an OTP? </span>
            <span className="font1">Resend</span>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Forgotpwd;
