import { useContext } from "react";
import { Cartpage } from "../Context";
import "./css/Cartlist.css";
import DeleteIcon from "@mui/icons-material/Delete";

const Cartlist = ({ items }) => {
  const { cart1, setCart1 } = useContext(Cartpage);

  return (
    <div className="clist-cont">
      <div className="clist-img">
        <img src={items.imgsrc} alt="loading.." loading="lazy"></img>
      </div>
      <div className="clist-body">
        <div className="clist-name">{items.name}</div>
        <div className="clist-price">
          <span>₹</span>
          {items.price}
        </div>
        <div className="clist-count">
          <input type="number"></input>
        </div>
      </div>
      <div className="clist-body1">
        <div>
          <DeleteIcon
            className="i-cart1"
            sx={{ fontSize: 24 }}
            onClick={() => {
              setCart1(cart1.filter((c) => c.id !== items.id));
            }}
          />
        </div>
        <div className="clist-tprice">
          <span>₹</span>
          {items.price}
        </div>
      </div>
    </div>
  );
};

export default Cartlist;
